	import {
        mapMutations
    } from 'vuex';
	import {registerByPass, smsCode} from "@/api/login";
	const graceChecker = require("@/common/graceChecker.js");
	export default{
		data(){
			return {
				superior:0,
				mobile: '',
				password: '',
				logining: false,
				smsCodeBtnDisabled: false,
				codeSeconds: 60
			}
		},
		onLoad(){
               this.superior=this.$route.query.id  ||0;
		},
		methods: {
			...mapMutations(['login']),
			blurMobileChange(e) {
				this.mobile = e.detail.value
				console.log(this.mobile)
			},
			blurPasswordChange(e) {
				this.password = e.detail.value
				console.log(this.password)
			},
			blurRePasswordChange(e) {
				if (this.password !== e.detail.value) {
			    this.$api.msg('两次输入的密码不一致');
				}
			},
			navBack(){
				uni.navigateBack();
			},
			navTo(url){
				// this.$api.msg('去注册');
				uni.navigateTo({
					url
				})
			},
			/**
			 *@des 获取手机验证码
			 *@author stav stavyan@qq.com
			 *@blog https://stavtop.club
			 *@date 2019/11/15 11:10:28
			 *@param mobile
			 */
			getSmsCode () {
				if (!this.checkPhoneIsValid(this.mobile)) return;
				/* this.$post(smsCode, {
					mobile: this.mobile,
					usage: 'register'
				}).then(r=>{ */
			    this.$api.msg("验证码发送成功, 验证码是: 123456") ;   //${r.data}`);
					this.smsCodeBtnDisabled = true;
					let time = 59;
					let timer = setInterval(() => {
						if(time === 0) {
							clearInterval(timer);
							this.smsCodeBtnDisabled = false;
						} else {
							this.codeSeconds = time;
							this.smsCodeBtnDisabled = true;
							time--
						 }
					},1000)
				/* }).catch(err => {
					console.log(err)
				}) */
			},
			/**
			 *@des 验证手机号是否有效
			 *@author stav stavyan@qq.com
			 *@blog https://stavtop.club
			 *@date 2019/11/15 11:54:25
			 *@param mobile 手机号
			 */
			checkPhoneIsValid (mobile) {
				if (!mobile.length < 0) {
			    this.$api.msg(`请输入11位的手机号`);
					return false;
				}
				const reg = /^1[0-9]{10,10}$/;
				if (!reg.test(mobile)) {
			    this.$api.msg(`请输入正确的手机号`);
					return false;
				} else {
					return true;
				}
			},
			async toRegister(e){
				const formData = e.detail.value;
				if (formData.password !== formData.password_repetition) {
			    this.$api.msg(`两次输入的密码不一致`);
					return;
				}
				const rule = [
					{name:"mobile", checkType : "phoneno", checkRule:"11,11",  errorMsg:"请输入正确的手机号"},
					{name:"code", checkType : "notnull", checkRule:"",  errorMsg:"请输入验证码"},
					{name:"password", checkType : "string", checkRule:"6,6",  errorMsg:"密码长度为6位"},
					{name:"password_repetition", checkType : "notnull", checkRule:"",  errorMsg:"请输入确认密码"},
					{name:"realname", checkType : "string", checkRule:"2,8",  errorMsg:"姓名应为2-8字符"}
				];
				const checkRes = graceChecker.check(formData, rule);
				if(!checkRes){
			    this.$api.msg(graceChecker.error);
					return;
				}
				let params = {}







				params.group = 'tinyShopWechatMq'




				console.log(params)
				console.log(formData)
				this.$post(registerByPass, {
					...params,
					...formData
				}).then(r=>{
			    this.$api.msg(`恭喜您注册成功`);
					uni.navigateTo({
						url: '/pages/public/login'
					})
				}).catch(err => {
					console.log(err)
				})
			}
		},

	}
